.intro {
  background-image: url("../img/bg-intro.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  height: 100vh;
  color: #ffffff;
}

.intro:before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  background-blend-mode: multiply;
  background-image: linear-gradient(rgba(18, 18, 18, 0.44), rgba(18, 18, 18, 0.44));
}

.intro .heading {
  margin-bottom: 25px;
}

.intro h1 {
  font-size: 50px;
  font-weight: 800;
  letter-spacing: 1.3px;
  width: 60%;
  margin-bottom: 30px;
  position: relative;
  z-index: 10;
}

.intro p {
  font-size: 16px;
  font-weight: 300;
  width: 60%;
  position: relative;
  z-index: 10;
  color: #ffffff;
}

.advantages {
  padding: 85px 0;
  background-color: #1d2832;
}

.advantages__block span {
  font-size: 36px;
  font-weight: 300;
  letter-spacing: 0.9px;
  color: #a48e75;
  margin-bottom: 15px;
  text-transform: uppercase;
  display: block;
}

.advantages__block h3 {
  font-size: 14px;
  font-weight: 600;
  line-height: 1.43;
  letter-spacing: 0.4px;
  color: #ffffff;
  text-transform: uppercase;
}

